.comment__title {
  width: 162px;
  padding-top: 28px;
  margin-left: -20px;
  padding-right: 10px;
  padding-left: 18px;
  padding-bottom: 35px;

  font-size: 24px;
  line-height: 24px;

  background-color: #ffffff;
}

.comment__description {
  width: 100%;
  height: 150px;
  padding: 0;
  padding-left: 15px;
  padding-right: 10px;
  padding-top: 12px;

  font-size: 16px;
  line-height: @text-height--small;
  color: @color-description;

  resize: none;
}

.comment__description::placeholder {
  font-size: 16px;
  line-height: 18px;
  color: @color-description;
  text-transform: uppercase;
}

@media (min-width: @tablet-width) {
  .comment {
    padding: 0 69px;
    margin-top: 37px;
  }

  .comment__title {
    width: 259px;
    margin-left: -40px;
    margin-bottom: 42px;
    padding-left: 40px;

    font-size: @text-size--big;
    line-height: @text-height--big;
  }

  .comment__description {
    height: 174px;
    padding: 0;
    padding-left: 19px;
    padding-right: 19px;
    padding-top: 9px;

    font-size: @text-size--medium;
    line-height: 30px;
  }

  .comment__description::placeholder {
    font-size: @text-size--medium;
    line-height: 30px;
  }
}

@media (min-width: @desktop-width) {
  .comment {
    width: 1300px;
    padding: 0 @desktop-padding;
    margin: 0 auto;
    margin-top: 67px;
  }

  .comment__title {
    padding-top: 0;
  }
}
