.site-list {
  display: flex;
  flex-direction: column;
  padding: 0;
  margin: 0;
  width: 100%;
  height: 100%;

  list-style: none;
}

.site-list__item a {
  display: block;
  padding: 22px 0;

  font-family: "Oswald", "Arial", sans-serif;
  font-size: @text-size--medium;
  line-height: @text-height--medium;
  text-transform: uppercase;
  font-weight: 500;
  color: @color-main-text;
}

.site-list a[href] {
  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

.site-list__item {
  text-align: center;

  border-top: 1px #f2f2f2 solid;
  border-bottom: 1px #f2f2f2 solid;
}

@media (min-width: @tablet-width) {
  .site-list {
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 440px;
    min-height: 44px;
    margin: 0;
  }

  .site-list__item {
    border: none;
  }

  .site-list__item a {
    padding: 17px 0;
    padding-bottom: 8px;

    font-size: 18px;
    line-height: 25px;
    color: #222222;
    font-weight: 400;
  }

  .site-list__item-active a {
    border-bottom: 2px #68b738 solid;
  }
}

@media (min-width: @desktop-width) {
  .site-list {
    min-width: 478px;
  }

  .site-list__item a {
    padding: 19px 0;
    padding-bottom: 9px;

    font-size: @text-size--medium;
  }

  .site-list__white a {
    color: #ffffff;
  }

  .site-list__item-active {
    border-bottom-color: #ffffff;
  }

  .site-list__item-active--white a {
    border-bottom: 2px #ffffff solid;
  }
}
